import {comingSoon,newMovies,top250,getInTheaters} from '../../pages/apis/index.js'
import {mapState} from 'vuex'
export default {
	data() {
		return {
			start:0,   // 请求数据，从多少开始请求
			moveList:[],  // 请求到电影列表
			isshow:false,  // 是否显示，"已经到底"
			typeNum:-1   // top250，即将上映的电影...的来源标识符
		}
	},
	onLoad(option) {
		console.log(option.index)
		this.typeNum = option.index
		uni.showLoading({
		    title: '加载中'
		})
		this.getClassData(this.typeNum)({
			city: this.city,
			start: '0',
			count:'10'
		}).then(res =>{
			let [err,data] = res;
			this.moveList = data.data.subjects
			uni.hideLoading()
			console.log(this.moveList)
		})
	},
	computed:{
		...mapState({
			city: state => state.city
		})
	},
	methods: {
		// 滚动条滚动到底部事件，滑动加载
		scrolltolower(){
			if(!this.isshow){
				this.start+=10;
				uni.showLoading({
				    title: '加载中',
					mask:true
				})
				console.log(this.start)
				this.getClassData(this.typeNum)({
					city: this.city,
					start: this.start,
					count:'10'
				}).then(res =>{
					let [err,data] = res;
					let subjects = data.data.subjects
					// 判断没有数据，停止请求接口
					if(subjects.length == 0){
						console.log(111)
						this.isshow = true
					}
					// 每次拼接10条数据
					this.moveList = this.moveList.concat(subjects)
					uni.hideLoading()
				})
			}else{
				uni.hideLoading()
			}
		},
		// 判断是即将上映的电影，还是top250...
		getClassData(state){
			if(state == 3){
				return getInTheaters
			}else{
				let fnList = [comingSoon,newMovies,top250]
				return fnList[state]
			}
		},
		// 跳转到页面详情事件
		toDetails(id){
			uni.navigateTo({
				url: '../details/details?id='+ id,
			});
		}
	}
}
